.ListItem {
    margin-bottom: 0.8vmin;
    margin-left: 1.5vmin;
}

.ListItem-header {
    align-items: center;
    background-color: darkgoldenrod;
    border: solid 4px goldenrod;
    border-radius: 100px;
    color: white;
    display: flex;
    font-size: xxx-large;
    font-weight: bold;
    min-height: calc(50px + 2vmax);
    justify-content: center;
    text-decoration-color: #282c34;
    text-transform: uppercase;
    width: 90vmin;
}

.ListItem-header > div {
    min-height: inherit;
}

.ListItem-header.is-revealed > div {
    width: inherit;
}

.ListItem-header.host-view {
    min-height: 40px;
    font-size: x-large;
}

.ListItem-header.host-view > div {
    width: inherit;
}

.ListItem-number {
    align-items: center;
    background-color: navy;
    border-radius: 100px;
    display: flex;
    justify-content: center;
    min-height: inherit;
}

.ListItem-answer {
    align-items: center;
    background-color: royalblue;
    border-radius: 100px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    min-height: inherit;
    width: 100%;
}

.ListItem-answer.host-view {
    font-size: smaller;
    min-height: inherit;
}

.ListItem-answer.host-view:not(.is-revealed) {
    background-color: darkgray;
}

.ListItem-answer.host-view:not(.is-revealed) .ListItem-number {
    background-color: gray;
}

.ListItem-answer.host-view:not(.is-revealed) .ListItem-points {
    background-color: gray;
}

.ListItem-answer:not(.host-view):not(.is-revealed) {
    display: none;
}

.is-revealed-enter {
    opacity: 0;
}

.is-revealed-enter-active {
    display: block;
    opacity: 1;
    transition: all 300ms;
}

.is-revealed-exit {
    opacity: 1;
}

.is-revealed-exit-active {
    opacity: 0;
    transition: all 300ms;
}

.ListItem-text {
    align-items: center;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    min-height: inherit;
    text-align: start;
}

.ListItem-text.smaller {
    font-size: xx-large;
}

.ListItem-text.smaller .ListItem-number {
    font-size: xxx-large;
}

.ListItem-text .ListItem-number.smaller {
    font-size: xx-large;
}

.ListItem-points {
    align-items: center;
    background-color: brown;
    border-radius: 100px;
    display: flex;
    justify-content: center;
    min-height: inherit;
    min-width: 8vmax;
}
